        {name: 'AI 경비원', group: 'aioffice', img:icoGuard, isClosed: true, isMouseOver:false},
        {name: 'AI영어교육', group: 'edu', img:icoEnglishEdu, isClosed: true, isMouseOver:false},
        {name: 'AI 회의실', group: 'aioffice', img:icoMeetingRoom, isClosed: false, isMouseOver:false},
        {name: '출퇴근 관리 키오스크', group: 'company', img:icoCommutingKiosk, isClosed: true, isMouseOver:false},
        {name: '무인카페 판매원', group: 'sales', img:icoUnmannedCafeSalesperson, isClosed: true, isMouseOver:false},
        {name: '상장사 IR 담당휴면 AI', group: 'company', img:icoListedCompanyIrAi, isClosed: false, isMouseOver:false},
        {name: '서빙로봇', group: 'aioffice', img:icoServingRobot, isClosed: true, isMouseOver:false},
        {name: '사내AI 아나운서', group: 'aioffice', img:icoAnnouncer, isClosed: true, isMouseOver:false},
        {name: 'AI 상담원', group: 'aioffice', img:icoCounselor, isClosed: true, isMouseOver:false},
        {name: '잡담친구', group: 'entertainment', img:icoSmallTalk, isClosed: true, isMouseOver:false},
        {name: 'AI 비서', group: 'aioffice', img:icoSecretary, isClosed: true, isMouseOver:false},
