  methods: {
    itemMouseOver: function(item) {
      item.isMouseOver = true
    },  
    itemMouseLeave: function(item) {
      item.isMouseOver = false
    },
    doStt: function() {
      if(!this.isSttOn) {
        this.isSttOn = !this.isSttOn
        let searchPlaceholder = document.querySelector('.search input')
        searchPlaceholder.placeholder="소리를 듣고 있어요."
        let micIcon = document.querySelector('.mic')
        micIcon.classList.add('pressed')
      }else{
        this.isSttOn = !this.isSttOn
        let searchPlaceholder = document.querySelector('.search input')
        searchPlaceholder.placeholder="검색어를 입력하시거나 말씀해 주세요."
        let micIcon = document.querySelector('.mic')
        micIcon.classList.remove('pressed')
      }
    },

    placeCircle: function(group) {
      this.groupName = group
      
      if(group == 'aioffice'){
        this.isLifeBtnActive = false
        this.isEduBtnActive = false
        this.isSalesBtnActive = false
        this.isCompanyBtnActive = false
        this.isEntertainmentBtnActive = false

        this.isOfficeBtnActive = true
      } else if(group == 'life') {
        this.isOfficeBtnActive = false
        this.isEduBtnActive = false
        this.isSalesBtnActive = false
        this.isCompanyBtnActive = false
        this.isEntertainmentBtnActive = false

        this.isLifeBtnActive = true
      } else if(group == 'edu') {
        this.isOfficeBtnActive = false
        this.isLifeBtnActive = false
        this.isSalesBtnActive = false
        this.isCompanyBtnActive = false
        this.isEntertainmentBtnActive = false

        this.isEduBtnActive = true
      } else if(group == 'sales') {
        this.isOfficeBtnActive = false
        this.isLifeBtnActive = false
        this.isEduBtnActive = false
        this.isCompanyBtnActive = false
        this.isEntertainmentBtnActive = false

        this.isSalesBtnActive = true
      } else if(group == 'company') {
        this.isOfficeBtnActive = false
        this.isLifeBtnActive = false
        this.isEduBtnActive = false
        this.isSalesBtnActive = false
        this.isEntertainmentBtnActive = false

        this.isCompanyBtnActive = true
      } else if(group == 'entertainment') {
        this.isOfficeBtnActive = false
        this.isLifeBtnActive = false
        this.isEduBtnActive = false
        this.isSalesBtnActive = false
        this.isCompanyBtnActive  = false

        this.isEntertainmentBtnActive= true
      }

      this.selectedArr = this.lists.filter((item) => item.group == group)
      this.leftOverArr = this.lists.filter((item) => item.group !== group)
      this.floorNum = Math.floor(this.leftOverArr.length / 1.8)
      this.selectedMidArr = this.leftOverArr.slice(0, this.floorNum)
      this.selectedBigArr = this.leftOverArr.slice(this.floorNum)
      this.subBlankCelArr = this.selectedBigArr.filter((item)=>{
        return item.name !== undefined
      })
      
      this.smallCircleLength = this.selectedArr.length
      this.midCircleLength = this.selectedMidArr.length
      this.bigCircleLength = this.subBlankCelArr.length
      
      // selectedBigArr 중에서 아래 언디파인 애들 3개를 빼줘서 다시 배열을 만들어줌
      // console.log('subBlankCelArr::',this.subBlankCelArr)
      
      // // 선택된 배열 갯수 이용 하여 자식 써클 만들기
      // for(let i = 0; i < this.selectedArr.length; i++){
      //   let smallCircle = document.createElement('div')
      //   smallCircle.setAttribute('class', 'smallCircle')
      //   circleContainer.appendChild(smallCircle)
      //   let smallCircles = document.querySelectorAll('.smallCircle')

      //   smallCircles[i].style.position = 'absolute'
      //   smallCircles[i].style.transform = `rotateX(${circleGap * i}deg) translate(12em) rotate(-${circleGap * i}deg)`
      //   console.log(smallCircles)
      // }

      setTimeout(()=> {
        let smallCircles = document.querySelectorAll('.smallCircle')
        let midCircles = document.querySelectorAll('.midCircle')
        let bigCircle = document.querySelectorAll('.bigCircle')

        let circleGap = 360 / this.smallCircleLength
        let midCircleGap = 360 / this.midCircleLength
        let bigCircleGap = 360 / this.bigCircleLength

        smallCircles.forEach((item,idx) =>
        item.style.transform = `rotate(${circleGap * idx -90}deg) translate(240px) rotate(-${circleGap * idx}deg)`
        )

        midCircles.forEach((item,idx) =>
        item.style.transform = `rotate(${midCircleGap * idx - 90}deg) translate(560px) rotate(-${midCircleGap * idx}deg)`
        )

        bigCircle.forEach((item,idx) =>
        item.style.transform = `rotate(${bigCircleGap * idx -90}deg) translate(840px) rotate(-${bigCircleGap * idx}deg)`
        )

        let itemBox = document.querySelectorAll('.itemBox')
        let smallCircleCount = 0
        let midCircleCount = 0
        let bigCircleCount = 0

        for(let i = 0; i < itemBox.length; i++){
          if(itemBox[i].dataset.group == group) {
            itemBox[i].style.transform = `translate3d(${smallCircles[smallCircleCount].getBoundingClientRect().left - this.getAbsoluteLeft(itemBox[i]) }px,${smallCircles[smallCircleCount].getBoundingClientRect().top - this.getAbsoluteTop(itemBox[i])}px, 0)`
            itemBox[i].firstElementChild.style.scale = '1.6' 
            smallCircleCount += 1
          } else {
            if(midCircleCount < this.selectedMidArr.length){
              itemBox[i].style.transform = `translate3d(${midCircles[midCircleCount].getBoundingClientRect().left - this.getAbsoluteLeft(itemBox[i]) }px,${midCircles[midCircleCount].getBoundingClientRect().top - this.getAbsoluteTop(itemBox[i])}px, 0)`
              itemBox[i].firstElementChild.style.scale = '1.04' 
              midCircleCount += 1
            }else if(itemBox[i].dataset.group !== undefined){
              itemBox[i].style.transform = `translate3d(${bigCircle[bigCircleCount].getBoundingClientRect().left - this.getAbsoluteLeft(itemBox[i]) }px,${bigCircle[bigCircleCount].getBoundingClientRect().top - this.getAbsoluteTop(itemBox[i])}px, 0)`
              itemBox[i].firstElementChild.style.scale = '0.6' 
              bigCircleCount += 1
            }
          }
        }
      },200)
    }, 
    getAbsoluteLeft: function(element) {
      let halfWidth = element.previousElementSibling.offsetWidth / 2
      return element.previousElementSibling.getBoundingClientRect().left + halfWidth
    },
    getAbsoluteTop: function(element) {
      let halfHeight = element.previousElementSibling.offsetHeight / 2
      return element.previousElementSibling.getBoundingClientRect().top + halfHeight
    },
    getEleCenter: function(ele) { 
      return ele.getBoundingClientRect().width / 2
    },
    resetGrid: function() {
      this.isOfficeBtnActive = false
      this.isEduBtnActive = false
      this.isSalesBtnActive = false
      this.isCompanyBtnActive = false
      this.isLifeBtnActive = false
      this.groupName=''
      let itemBox = document.querySelectorAll('.itemBox')
      itemBox.forEach(function(box) {
      box.style.transform = "translate3d(0, 0, 0)"
      box.firstElementChild.style.scale = "1" 
      })
    },

  },
  mounted() {
    let This = this
    window.onresize = function() {
      if(This.groupName.length > 0) {
      This.placeCircle(This.groupName)
      }
